import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        /**
         * @description 场景表 
         */
        scenario: [
            {
                'name': '场景一',
                'id': 'json.1.json'
            }
        ],
        /**
         * @description 任务表
         */
        tasks: [
            {
                icon: "data:image/png;base64,",
                name: "EMR",
                busiType: "emr"
            },
            {
                icon: "data:image/png;base64,",
                name: "XDATA",
                busiType: "xdata"
            },
            {
                icon: "data:image/png;base64,",
                name: "流计算",
                busiType: "streamcomputing"
            },
            {
                icon: "data:image/png;base64,",
                name: "日至云",
                busiType: "cloudlog"
            }
        ],
        /**
         * @description 属性表
         */
        propMap: {
            emr: {
                name: 'zhangsan',
                age: 14
            },
            xdata: {

            },
            streamcomputing: {
                
            },
            cloudlog: {

            }
        }
    },
    mutations: {

    },
    getters: {

    },
    actions: {

    }
});



